import { prisma } from '@/lib/prisma'; // ÂºïÂÖ• Prisma ÂÆ¢Êà∂Á´Ø
import { promises as fs } from 'fs'; // ÂºïÂÖ• Node.js ÁöÑÊ™îÊ°àÁ≥ªÁµ±Ê®°ÁµÑ
import path from 'path'; // ÂºïÂÖ• Node.js ÁöÑË∑ØÂæëÊ®°ÁµÑ

// Â¶ÇÊûúÊú™‰æÜÊúâÂñÆÁç®‰∏äÂÇ≥ÂúñÁâáÁöÑÈúÄÊ±ÇÔºåÂèØ‰ª•Â°´ÂÖÖÈÄôÂÄãÂáΩÊï∏
// ÁõÆÂâçÔºåÂúñÁâáËôïÁêÜÊúÉÊï¥ÂêàÂà∞ addMenuItem ‰∏≠
