/**
 * ÁÆ°ÁêÜÂì°Êñ∞Â¢ûËèúÂñÆÈ†ÖÁõÆ
 * Êé•Êî∂ FormData Áâ©‰ª∂ÔºåËôïÁêÜÂúñÁâá‰∏äÂÇ≥‰∏¶Â∞áËèúÂñÆË≥áË®äÂ≠òÂÖ•Ë≥áÊñôÂ∫´„ÄÇ
 * @param {FormData} formData - ÂåÖÂê´ËèúÂñÆÂêçÁ®±„ÄÅÂÉπÊ†ºÂíåÂúñÁâáÊ™îÊ°àÁöÑ FormData Áâ©‰ª∂„ÄÇ
 * @returns {Promise<{ success: boolean, message: string, data?: any }>}
 */
export const addMenuItem = async (formData) => {
  const name = formData.get('name');
  const price = formData.get('price');
  const imageFile = formData.get('image'); // Áç≤Âèñ File Áâ©‰ª∂

  // 1. Êï∏ÊìöÈ©óË≠â
  if (!name || typeof name !== 'string' || name.trim() === '') {
    return { success: false, message: 'ËèúÂñÆÂêçÁ®±ÊòØÂøÖÂ°´È†Ö„ÄÇ' };
  }
  if (!price || isNaN(parseFloat(price)) || parseFloat(price) <= 0) {
    return { success: false, message: 'ÂÉπÊ†ºÂøÖÈ†àÊòØÊúâÊïàÁöÑÊ≠£Êï∏„ÄÇ' };
  }
  if (!(imageFile instanceof File) || imageFile.size === 0) {
    return { success: false, message: 'ÂúñÁâáÊ™îÊ°àÊòØÂøÖÂ°´È†Ö„ÄÇ' };
  }

  try {
    // 2. ËôïÁêÜÂúñÁâáÂÑ≤Â≠òÂà∞ public Ë≥áÊñôÂ§æ
    // ÁÇ∫‰∫ÜÈÅøÂÖçÊ™îÂêçË°ùÁ™ÅÔºåÁµ¶ÂúñÁâáÁîüÊàê‰∏ÄÂÄãÂîØ‰∏ÄÊ™îÂêç
    const fileExtension = path.extname(imageFile.name); // Áç≤ÂèñÂéüÂßãÂâØÊ™îÂêç
    const uniqueFileName = `${Date.now()}-${Math.random().toString(36).substring(2, 8)}${fileExtension}`;
    
    // ÂÆöÁæ©ÂúñÁâá‰∏äÂÇ≥ÁöÑÁõÆÊ®ôÁõÆÈåÑÔºö/public/uploads
    // process.cwd() ËøîÂõû Node.js ÈÄ≤Á®ãÁöÑÁï∂ÂâçÂ∑•‰ΩúÁõÆÈåÑÔºàÂç≥Â∞àÊ°àÊ†πÁõÆÈåÑÔºâ
    const uploadDir = path.join(process.cwd(), 'public', 'uploads');
    const filePath = path.join(uploadDir, uniqueFileName); // ÂÆåÊï¥Ê™îÊ°àË∑ØÂæë

    // Ê™¢Êü•‰∏¶ÂâµÂª∫‰∏äÂÇ≥ÁõÆÈåÑ (Â¶ÇÊûú 'public/uploads' ‰∏çÂ≠òÂú®)
    await fs.mkdir(uploadDir, { recursive: true });

    // Â∞áÂúñÁâáÊ™îÊ°àÁöÑÁ∑©Ë°ùÂçÄÂØ´ÂÖ•Á£ÅÁ¢ü
    const bytes = await imageFile.arrayBuffer(); // Â∞á File Áâ©‰ª∂ËΩâÊèõÁÇ∫ ArrayBuffer
    const buffer = Buffer.from(bytes); // Â∞á ArrayBuffer ËΩâÊèõÁÇ∫ Node.js Buffer
    await fs.writeFile(filePath, buffer);

    // ÂúñÁâáÁöÑÂÖ¨Èñã URL Ë∑ØÂæëÔºåÈÄôÊòØ Next.js ÊúÉÊèê‰æõÁöÑË∑ØÂæë
    const imageUrl = `/uploads/${uniqueFileName}`;

    // 3. Â∞áËèúÂñÆË≥áË®äÂ≠òÂÖ•Ë≥áÊñôÂ∫´
    const newMenuItem = await prisma.MenuItem.create({ // Ê≥®ÊÑèÔºöÈÄôË£°ÊáâË©≤ÊòØ MenuItem Ê®°Âûã
      data: {
        name: name.trim(), // Ê∏ÖÈô§ÂêçÁ®±ÂâçÂæåÁ©∫ÁôΩ
        price: parseFloat(price), // Á¢∫‰øùÂÉπÊ†ºÊòØÊï∏Â≠óÈ°ûÂûã
        imageUrl, // Â≠òÂÑ≤ÂúñÁâáÁöÑÁõ∏Â∞çË∑ØÂæë
      },
    });

    // 4. ËøîÂõûÊàêÂäüË®äÊÅØ
    return { success: true, message: 'ËèúÂñÆÊñ∞Â¢ûÊàêÂäüÔºÅ', data: newMenuItem };

  } catch (error) {
    console.error('Êñ∞Â¢ûËèúÂñÆÂ§±Êïó:', error);

    // ËôïÁêÜ Prisma ÁâπÂÆöÁöÑÈåØË™§Ôºå‰æãÂ¶ÇÂêçÁ®±ÈáçË§á (P2002)
    if (error.code === 'P2002' && error.meta?.target?.includes('name')) {
      return { success: false, message: 'ËèúÂñÆÂêçÁ®±Â∑≤Â≠òÂú®ÔºåË´ã‰ΩøÁî®‰∏çÂêåÁöÑÂêçÁ®±„ÄÇ' };
    }
    // ÂÖ∂‰ªñ‰∏ÄËà¨ÈåØË™§
    return { success: false, message: `Êñ∞Â¢ûËèúÂñÆÊôÇÁôºÁîüÈåØË™§: ${error.message || 'Êú™Áü•ÈåØË™§'}` };
  }
};

