"use client";

import { useEffect, useState } from "react";
import { useSession } from "next-auth/react";
import { getMqttClient } from "@/lib/mqttClient";
import { getMenuItems } from "@/app/actions/menu";
import { addOrder } from "@/app/actions/order";
import {
  getOrderCheckoutTopic,
  getOrderStatusWildcardTopic,
} from "@/utils/mqttTopic";

export default function CheckoutPage() {
  const { data: session, status } = useSession();
  const [cart, setCart] = useState([]);
  const [menuItems, setMenuItems] = useState([]);
  const [specialRequests, setSpecialRequests] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [topic, setTopic] = useState("");
  const [statusLog, setStatusLog] = useState([]);

  useEffect(() => {
    if (status === "loading") return;

    if (status === "unauthenticated") {
      alert("Ë´ãÂÖàÁôªÂÖ•");
      window.location.href = "/login";
      return;
    }

    const savedCart = sessionStorage.getItem("cart");
    if (savedCart) {
      setCart(JSON.parse(savedCart));
    } else {
      window.location.href = "/";
      return;
    }

    const fetchMenu = async () => {
      try {
        let data = await getMenuItems();
        if (!data) {
          const response = await fetch("/api/menu");
          data = await response.json();
        }
        setMenuItems(data);
      } catch (err) {
        alert("ÂèñÂæóËèúÂñÆÂ§±Êïó");
      }
    };

    setTopic(getOrderCheckoutTopic());
    fetchMenu();
  }, [status]);

  // ‚úÖ MQTT Ë®ÇÈñ±ÔºöË®ÇÂñÆÁãÄÊÖãÊõ¥Êñ∞
  useEffect(() => {
    if (!session?.user?.id) return;
    const client = getMqttClient();
    const statusTopic = getOrderStatusWildcardTopic(session.user.id);

    const handleMessage = (t, message) => {
      try {
        const payload = JSON.parse(message.toString());
        console.log("üì• MQTT Ë®ÇÂñÆÁãÄÊÖãÊõ¥Êñ∞:", payload);
        setStatusLog((prev) => [...prev, payload]);
      } catch (err) {
        console.error("‚ùå Ëß£Êûê MQTT Ë®äÊÅØÈåØË™§:", err);
      }
    };

    client.subscribe(statusTopic, (err) => {
      if (err) console.error("‚ùå Ë®ÇÈñ±Â§±Êïó:", err);
      else console.log("üì° Ë®ÇÈñ±:", statusTopic);
    });

    client.on("message", handleMessage);

    return () => {
      client.unsubscribe(statusTopic);
      client.removeListener("message", handleMessage);
    };
  }, [session]);

  const getTotalPrice = () => {
    return cart.reduce((total, cartItem) => {
      const menuItem = menuItems.find((item) => item.id === cartItem.id);
      return total + (menuItem?.price || 0) * cartItem.quantity;
    }, 0);
  };

  const handleSubmitOrder = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      const orderItems = cart.map((item) => ({
        menuItemId: item.id,
        quantity: item.quantity,
        specialRequest: specialRequests[item.id] || "",
      }));

      const customerId = session?.user?.id;
      let orderData = await addOrder({ orderItems, customerId });

      if (!orderData) {
        const response = await fetch(`/api/order/checkout`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ orderItems, customerId }),
        });

        if (!response.ok) {
          alert("ÈÄÅÂá∫Ë®ÇÂñÆÂ§±Êïó");
          return;
        }
        orderData = await response.json();
      }

      // ‚úÖ Áôº‰Ωà MQTT Ë®ÇÂñÆË®äÊÅØÁµ¶ÂªöÊàø
      const client = getMqttClient();
      client.publish(topic, JSON.stringify(orderData));
      console.log("üì§ Â∑≤Áôº‰ΩàË®ÇÂñÆËá≥:", topic);

      sessionStorage.removeItem("cart");
      window.location.href = "/orders";
    } catch (err) {
      alert("ÈÄÅÂá∫Ë®ÇÂñÆÂ§±Êïó");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="container mx-auto p-6">
      <h1 className="text-3xl font-bold text-gray-800 mb-8 text-center">Á¢∫Ë™çË®ÇÂñÆ</h1>

      {cart.length === 0 ? (
        <div className="text-center text-gray-500 text-lg mt-20">
          Ë≥ºÁâ©ËªäÁõÆÂâçÊòØÁ©∫ÁöÑÔºåË´ãÂÖàÈÅ∏ÊìáÈ§êÈªû„ÄÇ
        </div>
      ) : (
        <form
          onSubmit={handleSubmitOrder}
          className="max-w-2xl mx-auto bg-white shadow-lg rounded-xl p-6 space-y-6"
        >
          <h2 className="text-xl font-semibold text-gray-700">Ë®ÇÂñÆÊòéÁ¥∞</h2>
          <ul className="divide-y">
            {cart.map((cartItem) => {
              const menuItem = menuItems.find((item) => item.id === cartItem.id);
              if (!menuItem) return null;
              return (
                <li key={cartItem.id} className="py-4 space-y-2">
                  <div className="flex justify-between items-center">
                    <span className="text-gray-800 font-medium">
                      {menuItem.name} √ó {cartItem.quantity}
                    </span>
                    <span className="text-right font-semibold text-gray-700">
                      ${ (menuItem.price * cartItem.quantity).toFixed(2) }
                    </span>
                  </div>
                  <div>
                    <label
                      htmlFor={`special-request-${cartItem.id}`}
                      className="block text-sm text-gray-500 mb-1"
                    >
                      ÂÇôË®ªÔºàÂèØÈÅ∏Ôºâ
                    </label>
                    <textarea
                      id={`special-request-${cartItem.id}`}
                      className="w-full border rounded-md p-2 text-sm text-gray-700 focus:ring-2 focus:ring-blue-300 resize-none"
                      rows={2}
                      placeholder="‰æãÂ¶ÇÔºöÂéªÂÜ∞„ÄÅÂ∞ëÁ≥ñ..."
                      value={specialRequests[cartItem.id] || ""}
                      onChange={(e) =>
                        setSpecialRequests((prev) => ({
                          ...prev,
                          [cartItem.id]: e.target.value,
                        }))
                      }
                    />
                  </div>
                </li>
              );
            })}
          </ul>

          <div className="border-t pt-4 text-lg font-bold flex justify-between">
            <span>Á∏ΩÈáëÈ°çÔºö</span>
            <span>${getTotalPrice().toFixed(2)}</span>
          </div>

          <button
            type="submit"
            disabled={isSubmitting || cart.length === 0}
            className="w-full bg-gradient-to-r from-pink-500 to-red-500 text-white py-3 rounded-md shadow hover:opacity-90 disabled:bg-gray-400 transition duration-300"
          >
            {isSubmitting ? "Ê≠£Âú®ÈÄÅÂá∫Ë®ÇÂñÆ..." : "ÈÄÅÂá∫Ë®ÇÂñÆ"}
          </button>

          {/* ‚úÖ È°ØÁ§∫Ë®ÇÂñÆÁãÄÊÖãÊõ¥Êñ∞ */}
          {statusLog.length > 0 && (
            <div className="mt-6 border-t pt-4">
              <h3 className="text-md font-semibold mb-2">Ë®ÇÂñÆÁãÄÊÖãÊõ¥Êñ∞Ôºö</h3>
              <ul className="text-sm text-gray-600 list-disc pl-5 space-y-1">
                {statusLog.map((log, index) => (
                  <li key={index}>
                    {log.status}Ôºà{new Date(log.updatedAt || Date.now()).toLocaleTimeString()}Ôºâ
                  </li>
                ))}
              </ul>
            </div>
          )}
        </form>
      )}
    </div>
  );
}
